
@import "kernel";

.password {
    @include component();

    .show-password {
        background: none;
        border: none;
        outline: none;
        display: block;
        padding: 2px;
        border-radius: 50%;
        margin: 0;
        will-change: box-shadow;
        height: 32px;
        transform: scale(1);
        position: relative;
        cursor: pointer;
        background: rgba(#fff, .8);

        svg {
            fill: $color-border;
            width: 18px;
            height: 18px;
        }

        &:before {
            content: '';
            position: absolute;
            top: 50%;
            left: 50%;
            width: 2px;
            height: 2px;
            border-radius: 50%;
            box-shadow: 0 0 0 0 rgba($color-main, 1);
            opacity: 0;
            margin: -1px 0 0 -1px;
        }

        &:active {
            transform: scale(.9);
        }
    }

    &.password-visible {
        .show-password {
            transform: scale(1);
            transition: transform .2s $ui-animation-swift;

            svg {
                fill: $color-main !important;
            }

            &:before {
                transition: opacity .1s $ui-animation-swift, box-shadow .5s ease;
                opacity: 1;
                box-shadow: 0 0 1px 18px rgba($color-main, 0);
            }
        }
    }
}



