
@import "kernel";

.input {
    @include component();

    .container {
        height: $ui-height;
        line-height: $ui-height;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        flex-wrap: nowrap;
        background: $color-white;
        position: relative;
        will-change: background, box-shadow;
        box-shadow: 0 0 0 1px $color-border;
        border-radius: 2px;
        z-index: 1;

        & /deep/ .loader {
            border-color: $color-main;
            .spinner {
                box-shadow: inset 0 0 0 2px $color-border;
            }
        }
    }

    input {
        height: inherit;
        display: block;
        float: left;
        width: 100%;
        box-sizing: border-box;
        padding: 0 0 0 $ui-padding;
        outline: none;
        border: none;
        background: none;
        margin: 0;
        appearance: textfield;
        color: $color-text;
        transition: color .2s $ui-animation-swift;
        position: relative;
        z-index: 2;
    }

    .placeholder {
        top: 1px;
        left: $ui-padding + 1px;
        width: 100%;
        font-size: $font-size;
        height: inherit;
        user-select: none;
        position: absolute;
        pointer-events: none;
        will-change: transform, color;
        color: $color-description;
        transform: translate(0, 0) scale(1);
        transform-origin: 0 50%;
        transition: transform .3s $ui-animation-swift .2s,
        color .3s $ui-animation-swift .2s;
        z-index: 3;
    }

    .icons {
        position: relative;
        height: inherit;
        padding: 0 0 0 10px;
        display: flex;
        justify-content: flex-end;
        flex-wrap: nowrap;
        align-items: center;
        float: right;
        z-index: 3;
        color: $color-description;

        & > * {
            margin-right: 10px !important;
        }
    }

    &.disabled {
        .container {
            background: $color-light-disabled !important;
            box-shadow: 0 0 0 1px $color-light-disabled-text !important;
            transition: background .3s $ui-animation-swift, box-shadow .1s $ui-animation-swift;
        }

        input,
        .placeholder {
            color: $color-light-disabled-text !important;
            transition: color .3s $ui-animation-swift;
        }
    }

    &.active {
        .placeholder {
            color: $color-text;
            transform: translate(-16px, -32px) scale(.82);
            transition: transform .3s cubic-bezier(0.245, 1.600, 0.560, 0.925),
            color .3s $ui-animation-swift;
        }
    }

    &.focus {
        z-index: 5;

        .container {
            transition: box-shadow .1s $ui-animation-swift;
            box-shadow: 0 0 0 1px $color-main;
        }
    }
}



